// !COLORS
:root {
	--color-a: #e42320;
	--color-b: #060607;
	--color-c: #f3f3f3;
	--color-d: #b6b6b6;
	--color-e: #fff;
	--color-f: #8f96b0;
	--color-g: #616161;
	--color-h: #E2E2E2;
	--color-i: #222631;
}
// !Offset
$primaryOffset: 10;
$mobileOffset: 2;

// !Fonts Size
$font-size-xss: 12;
$font-size-xs: 14;
$font-size-s: 16;
$font-size-m: 18;
$font-size-l: 24;
$font-size-xl: 26;
$font-size-xxl: 32;
$font-size-xxxl: 38;
$font-size-bxl: 56;

// !Button Size
$button-size-xs: 42;
$button-size-s: 200;
$button-size-m: 286;
$button-size-l: 378;

// !Icon Size
$icon-size-xs: 18;
$icon-size-s: 24;
$icon-size-m: 32;
$icon-size-l: 48;
$icon-size-xl: 50;
$icon-size-xxl: 87;
$icon-size-xxxl: 105;
$icon-size-bxl: 135;

// !POSITION

// ? center
[class*='--center'] {
	display: flex;
	justify-content: center;
	align-items: center;
}
// !THEME

// ? theme-a
[class*='--theme-a'] {
	line-height: 112%;
	font-weight: 600;
	font-size: 18px;
}

// !SIZE
$maxWidth: 1920;

// !FONTS
$font-family-default: 'HelveticaNeueCyr', sans-serif;
// $font-family-Gilroy: 'Gilroy', sans-serif;
// $font-family-PFBeauSansPro: 'PFBeauSansPro', sans-serif;

// !BREAKPOINTS
$maxWidthContainer: 1200;
$md1: $maxWidthContainer + 40;
$md2: 991.98;
$md3: 767.98;
$md4: 600.98;

// !Transition
$transition-time: 0.3s;

//TODO : Technical var

//! Desktop
// ? Offset
$offset-05: $primaryOffset / 2;
$offset-1: $primaryOffset;
$offset-2: $primaryOffset * 2;
$offset-3: $primaryOffset * 3;
$offset-4: $primaryOffset * 4;
$offset-5: $primaryOffset * 5;
$offset-6: $primaryOffset * 6;
$offset-7: $primaryOffset * 7;
$offset-8: $primaryOffset * 8;
$offset-9: $primaryOffset * 9;
$offset-10: $primaryOffset * 10;
$offset-11: $primaryOffset * 11;

//! Mobile
// ? Offset
$mobileOffset: 2;
$offsetMobile-05: $offset-05;
$offsetMobile-1: $offset-1 / 2;
$offsetMobile-2: $offset-2 / 2;
$offsetMobile-3: $offset-3 / 2;
$offsetMobile-4: $offset-4 / 2;
$offsetMobile-5: $offset-5 / 2;
$offsetMobile-6: $offset-6 / 2;
$offsetMobile-7: $offset-7 / 2;
$offsetMobile-8: $offset-8 / 2;
$offsetMobile-9: $offset-9 / 2;
$offsetMobile-10: $offset-10/ 2;
$offsetMobile-11: $offset-11 / 2;
