html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  vertical-align: baseline;
  margin: 0;
  border: 0;
  padding: 0;
  font-weight: inherit;
  font-size: inherit; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

html {
  height: 100%; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: ''; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

input[type='time'] ::-webkit-calendar-picker-indicator,
::-webkit-datetime-edit-fields-wrapper,
::-webkit-inner-spin-button {
  padding-left: 100%; }

:root {
  --color-a: #e42320;
  --color-b: #060607;
  --color-c: #f3f3f3;
  --color-d: #b6b6b6;
  --color-e: #fff;
  --color-f: #8f96b0;
  --color-g: #616161;
  --color-h: #E2E2E2;
  --color-i: #222631; }

[class*='--center'] {
  display: flex;
  justify-content: center;
  align-items: center; }

[class*='--theme-a'] {
  line-height: 112%;
  font-weight: 600;
  font-size: 18px; }

* {
  box-sizing: border-box;
  outline: none;
  color: var(--color-b);
  letter-spacing: 0; }
  * ::selection {
    background: #acff05bb; }

html,
body {
  width: 100%;
  font-family: "HelveticaNeueCyr", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.18;
  letter-spacing: 0.1em;
  color: var(--color-b);
  position: relative; }

.input-validation--invalid {
  color: var(--color-a); }

.up {
  position: fixed;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: 0.3s opacity;
  right: 50px;
  bottom: 77px;
  width: 42px !important;
  min-height: 42px !important; }
  .up--active {
    opacity: 1;
    visibility: visible;
    pointer-events: auto; }
  .up .Button__icon-svg {
    margin-right: 0; }

._box-shadow,
._bs {
  box-shadow: 0px 5px 15px rgba(143, 150, 176, 0.3); }

body.noscroll {
  overflow: hidden; }

.blackout {
  position: fixed;
  z-index: 550;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0);
  pointer-events: none;
  transition: 0.3s; }
  .blackout--active {
    background-color: rgba(0, 0, 0, 0.5); }

.container {
  position: relative;
  overflow: hidden; }

[class*='--container'] {
  margin: 0 auto; }

[class*='--max-width'] {
  min-width: 320px;
  max-width: 1200px; }
  @media screen and (max-width: 1240px) {
    [class*='--max-width'] {
      padding: 0 20px; } }
  @media screen and (max-width: 991.98px) {
    [class*='--max-width'] {
      padding: 0 15px; } }

[class*='--grid-a'] {
  display: grid;
  grid-template-columns: 1fr; }

[class*='--grid-b'] {
  display: grid;
  grid-template-columns: repeat(2, 1fr); }

[class*='--grid-c'] {
  display: grid;
  grid-template-columns: repeat(3, 1fr); }

[class*='--grid-d'] {
  display: grid;
  grid-template-columns: repeat(4, 1fr); }

[class*='--grid-e'] {
  display: grid;
  grid-template-columns: repeat(5, 1fr); }

[class*='--grid-f'] {
  display: grid;
  grid-template-columns: repeat(6, 1fr); }

[class*='--grid-g'] {
  display: grid;
  grid-template-columns: repeat(4, 1fr); }

[class*='--grid-i'] {
  display: grid;
  grid-template-columns: repeat(2, 1fr); }

.fancybox-close-small {
  width: 52px;
  height: 52px; }

.fancybox-container .popup-discount {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, 15%); }

.fancybox-active {
  overflow: hidden;
  height: 100vh;
  padding-right: 10px !important; }
  .fancybox-active .container .header {
    padding-right: 10px !important; }
  .fancybox-active .container .up {
    margin-right: 10px !important; }

[class*='--scroll-h'] {
  overflow-x: auto;
  padding-bottom: 9px; }
  [class*='--scroll-h']::-webkit-scrollbar {
    width: 5px;
    /* ширина для вертикального скролла */
    height: 5px;
    /* высота для горизонтального скролла */
    background-color: transparent; }

::-webkit-scrollbar {
  width: 10px;
  /* ширина для вертикального скролла */
  height: 10px;
  /* высота для горизонтального скролла */
  background-color: transparent; }

/* ползунок скроллбара */
::-webkit-scrollbar-thumb {
  background-color: var(--color-h); }

::-webkit-scrollbar-thumb:hover {
  background-color: var(--color-d);
  cursor: pointer; }

/* Стрелки */
::-webkit-scrollbar-button:vertical:start:decrement {
  background: transparent;
  background-color: transparent;
  display: none; }

::-webkit-scrollbar-button:vertical:end:increment {
  background: transparent;
  background-color: transparent;
  display: none; }

::-webkit-scrollbar-button:horizontal:start:decrement {
  background: transparent;
  background-color: transparent;
  display: none; }

::-webkit-scrollbar-button:horizontal:end:increment {
  background: transparent;
  background-color: transparent;
  display: none; }

* {
  scrollbar-color: var(--color-b) transparent;
  /* «цвет ползунка» «цвет полосы скроллбара» */
  scrollbar-width: thin;
  /* толщина */ }
